<template>
  <div class="user">
    <div class="file-edit">
      <el-input placeholder="" 
      v-model="searchUser" 
      ref="searchUser"
      style="width: 230px;" 
      @keyup.enter.native="handleFilter">
      <el-button slot="append" icon="el-icon-search" @click="handleFilter"></el-button>
      </el-input>
      <el-button-group>
        <el-button v-waves type="primary" icon="plus" @click="handlerAdd"></el-button>
        <el-button v-waves type="primary" icon="plus" @click="handlerAddMore"></el-button>
      </el-button-group>
    </div>
    <el-row>
      <el-col class="right" :span="24" style='margin-top:15px;'>
        <el-card shadow="always">
          <el-table
          :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
          height="390px"
          border
          fit
          stripe>
            <el-table-column
            sortable
            :sort-method="sort"
            label=""
            prop="user_name"
            align='center'></el-table-column>
            <el-table-column
            label=""
            prop="real_name"
            align='center'></el-table-column>
            <el-table-column
            label=""
            prop="user_code"
            align='center'></el-table-column>
            <el-table-column
            label=""
            prop="user_level"
            align='center'>
            <template slot-scope="scope">
              <span v-if="scope.row.user_level === 0"></span>
              <span v-if="scope.row.user_level === 1"></span>
              <span v-if="scope.row.user_level === 2"></span>
              <span v-if="scope.row.user_level === 3"></span>
              <span v-if="scope.row.user_level === 4"></span>
            </template>
            </el-table-column>
            <el-table-column
            label=""
            prop="user_position"
            align='center'></el-table-column>
            <el-table-column
            label=""
            prop="dep_id"
            align='center'></el-table-column>
            <el-table-column
            label=""
            prop="is_company_leader"
            align='center'>
            <template slot-scope="scope">
              <span v-if="scope.row.is_company_leader === 0"></span>
              <span v-if="scope.row.is_company_leader === 1"></span>
            </template>
            </el-table-column>
            <el-table-column
            label=""
            prop="order_number"
            align='center'></el-table-column>
            <el-table-column
            label="email"
            prop="user_email"
            align='center'></el-table-column>
            <el-table-column
            label=""
            prop="stauts"
            align='center'>
            <template slot-scope="scope">
              <span v-if="scope.row.stauts === 0"></span>
              <span v-if="scope.row.stauts === 1"></span>
            </template>
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            width="120">
            <template slot-scope="scope">
              <el-button
                v-if="scope.row.stauts === 0"
                v-waves
                @click.native.prevent="handl(scope.row)"
                type="text"
                size="small">
                
              </el-button>
              <el-button
              v-waves 
              type="text"
              size="small"
              @click="handlerEdit(scope.row)">
                
              </el-button>
              <el-button
                v-if="scope.row.stauts === 1"
                v-waves
                @click.native.prevent="handler(scope.row)"
                type="text"
                size="small">
                
              </el-button>
            </template>
          </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <div>
      <el-pagination
      v-show="tableData.length > 0"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length">
      </el-pagination>
    </div>
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" 
    lock-scroll @close="close">
      <el-form :model="form" inline ref="form" :rules="rules" label-position="left">
        <el-form-item label=":" prop="user_name">
          <el-input v-model="form.user_name" ></el-input>
        </el-form-item>
        <el-form-item label=":" prop="user_password">
          <el-input v-model="form.user_password" ></el-input>
        </el-form-item>
        <el-form-item label=":" prop="real_name">
          <el-input v-model="form.real_name" ></el-input>
        </el-form-item>
        <el-form-item label=":" prop="user_code">
          <el-input v-model="form.user_code" ></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form.user_level" placeholder="">
            <el-option
              v-for="item in user_level"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form.is_company_leader" placeholder="">
            <el-option
              v-for="item in is_company_leader"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form.dep_id" placeholder="">
            <el-option
              v-for="item in dep_id"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.user_position"></el-input>
        </el-form-item>
        <el-form-item label=":" >
          <el-input v-model="form.order_number"></el-input>
        </el-form-item>
        <el-form-item label="email:" >
          <el-input v-model="form.user_email"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="form.status" placeholder="">
            <el-option
              v-for="item in status"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')"> </el-button>
        <el-button v-waves  v-if="dialogStatus=='create'" type="primary" @click="create('form')"> </el-button>
        <el-button v-waves  v-else type="primary" @click="update('form')"> </el-button>
      </div>
    </el-dialog>
    <excle :show.sync="show"></excle>
  </div>
</template>
<script>
import excle from "./components/excle"
import { fetchUser, addUser, updateUser, deleteUser, select, upd } from '@/api/admin/user/index'
import waves from '@/directive/waves' // 
import { getAll } from '@/api/admin/department/index'
export default {
  directives: {
    waves
  },
  components: {
    excle
  },
  data() {
    return {
      dep_id: [],
      stauts: [{
        value: 0,
        label: ''
      },{
        value: 1,
        label: ''
      }],
      user_level: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        },
        {
          value: 4,
          label: ''
        }],
        is_company_leader: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }],
      searchUser: undefined,
      loading: true,
      currentPage: 1,
      pagesize: 20,
      show: false,
      dialogFormVisible: false,
      dialogStatus: undefined,
      textMap: {
        create: '',
        update: ''
      },
      tableData: [],
      form: {
        user_name: undefined,
        user_password: undefined,
        user_code: undefined,
        real_name: undefined,
        user_level: undefined,
        is_company_leader: undefined,
        user_position: undefined,
        order_number: undefined,
        user_email: undefined,
        status: 0
      },
      rules: {
        user_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        user_password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        real_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        user_code: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.fetch()
  },
  methods: {
    sort() {

    },
    close() {
      this.loading = false
    },
    handleFilter() {
      this.$refs[searchUser].validate( valid => {
        if(valid) {
          select(searchUser).then( data => {
            this.tableData
            fgfgjdgnfn
          })
        }else {
          this.$notify({
              title: '',
              message: '',
              type: 'error',
              duration: 2000
            })
        }
      })
    },
    handleSizeChange(size) {
        this.pagesize = size;
    },
    handleCurrentChange(currentPage){
        this.currentPage = currentPage;
    },
    fetch() {
      getAll().then(data => {
        this.dep_id = data.data.data
      }),
      fetchUser().then(data => {
        this.tableData = data.data.data
        console.info(this.tableData)
        setTimeout(() => {
          this.loading = false
        }, 1.5 * 1000)
      })
    },
    handler(form) {

    },
    handlerAddMore() {
      this.show = true
    },
    cancel(form) {
      this.dialogFormVisible = false;
      this.$refs[form].resetFields();
    },
    handlerAdd() {
      this.dialogStatus = 'create'
      this.restForm()
      this.dialogFormVisible = true
    },
    handl(row) {
      const data = []
      data.push(row)
      deleteUser(data).then(() => {
        this.fetch()
        this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000
            })
      })
    },
    create(form) {
      this.$refs[form].validate(valid => {
        if(valid) {
          this.loading = true
          this.form.dep_id = this.form.dep_id.toString()
          addUser(this.form).then(() => {
            console.info("")
            this.loading = false
            this.dialogFormVisible = false;
            this.fetch()
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000
            })
            setTimeout(() => {
              this.loading = false
            }, 1.5 * 1000)
          })
        } else {
          this.loading = false
          return false
        }
      })
    },
    handlerEdit(row) {
      this.form = Object.assign({}, row)
      this.dialogStatus = 'update'
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs.form.clearValidate()
    })
  },
    update(form) {
    this.$refs.form.validate(valid => {
      if(valid) {
        this.loading = true
        this.form.dep_id = this.form.dep_id.toString()
        upd(this.form).then(response => {
          console.log(response)
          
            this.loading = false
            this.dialogFormVisible = false
            this.fetch()
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000
            })
            this.loading = false
          }
        )} else {
        this.loading = false
        return false
      }
    })
  },
    cancel(form) {
      this.dialogFormVisible = false;
      this.$refs[form].resetFields();
    },
    restForm() {
      this.form = {
        user_name: undefined,
        user_password: undefined,
        user_code: undefined,
        real_name: undefined,
        user_level: 1,
        is_company_leader: 1,
        user_position: undefined,
        order_number: undefined,
        user_email: undefined,
        status: '0'
      }
    },
  }
 }
</script>
<style scoped>

</style>



