import axios from '@/utils/fetch'

/**
 * 
 * @param {user} data
 */
export function addUser (data) {
  return axios({
    url: '/insertUser',
    method: 'post',
    data
  })
}

/**
 * 
 * @param {*} query
 */
export function fetchUser () {
  return axios({
    url: '/selectUserNameId',
    method: 'get'
  })
}

/**
 * 
 * @param {*} data
 */
export function deleteUser (data) {
  return axios({
    url: '/deleteUser' ,
    method: 'post',
    data
  })
}
/**
 * 
 * @param {*} data
 */
export function up (data) {
  return axios({
    url: '/MangImportUser',
    method: 'post',
    data
  })
}

/**
 * 
 * @param {*} data
 */
export function select (data) {
  return axios({
    url: '/selectUserByRealUsername',
    method: 'post',
    data
  })
}

/**
 * 
 * @param {*} data
 */
export function upd (data) {
  return axios({
    url: '/updateUser',
    method: 'post',
    data
  })
}



